import React from 'react';
import '../../sass/main.scss'

const Privacy = () => {
     
 return <>
<div style={{ backgroundColor:"#fff"}}>
                   <div style={{ padding: "50px"}}>
                      <h3>POLITIKA PRIVATNOSTI</h3>
                      <p>Mi smo jedini vlasnici informacija koje se nalaze na sajtu. Imamo informacije samo one koje nam Vi samovoljno date bilo mailom bilo direktnim kontktom. Ove informacije ne mogu biti predmet trgovine. Informacije koje prikupljamo od Vas prilikom kupovine su:</p>
                  <ul>
                      <li> Ime i prezime</li>
                      <li> E-mail adresa</li>
                      <li> Adresa isporuke artikla</li>
                      <li> Broja telefona</li>
                      <li> Ime i prezime</li>
                  </ul>
                  <p>Ove informacije emo koristiti da odgovorimo Vaim zahtevima zbog kojih ste nas kontaktirali. Neemo ih davati nikome van nae firme osim kada je to neophodno da bi se ispunili Vai zahtevi, npr.da poaljemo porudbinu.</p>
                  <p>Ukoliko nam ne kaete drugaije, u budunosti Vas moemo kontaktirati mailom da bi smo Vas obavestili o promocijama, popustima, novitetima, promenama na uslovima.</p>
                  <p>Imate mogunost da prekinete bilo koji kontakt sa nama. Moete uraditi sledee, bilo kada kontaktirajui nas, bilo mailom ili telefonom datim na sajtu:</p>
                  <ul>
                      <li>Imati uvid u nae informacije o Vama, ako uopte i postoje</li>
                      <li>Promeniti, ispraviti, informacije koje posedujemo o Vama</li>
                      <li>Naterati nas da izbriemo bilo koje informacije koje posedujemo o Vama</li>
                      <li>Izraziti zabrinust oko korienja informacija koje posedujemo o vama</li>
                  </ul>
                  <p>Posebnu panju posveujemo uvanju informacija o Vama. Kada nam poverite osetljive informacije putem sajta iste su zatiene online i offline.

</p>
<p>Prilikom unoenja podataka o platnoj kartici, poverljive informacija se prenose putem javne
mree u zatienoj (kriptovanoj) formi upotrebom SSL protokola i PKI sistema, kao trenutno
najsavremenije kriptografske tehnologije.
Sigurnost podataka prilikom kupovine, garantuje procesor platnih kartica, Banca Intesa ad
Beograd, pa se tako kompletni proces naplate obavlja na stranicama banke. Niti jednog
trenutka podaci o platnoj kartici nisu dostupni naem sistemu.</p>
<p>Kada nam dajete osetljivu informaciju (br.kreditne kartice) informacija se ifruje i prosleuje nama sigurnim putem (https na poetku adarese sajta).</p>
                  
                  <p>
                  Vae informacije su bezbedne i offline. Serveri na kojima se uvaju informacije su na sigurnom.

Ukoliko imate bilo kakva pitanja ili nedoumice oko zatite privatnosti ili oko podataka kojima raspolaemo molimo Vas da nas kontaktirate.
                  </p>
                  
                  
                  
                   </div>
              </div>
        </>
}

export default Privacy;
